"use strict";

var agateBtn = document.getElementById("agate-button");
var agateModal = document.getElementById("agate-modal");
var tourmalineBtn = document.getElementById("tourmaline-button");
var tourmalineModal = document.getElementById("tourmaline-modal");
var rhodoniteBtn = document.getElementById("rhodonite-button");
var rhodoniteModal = document.getElementById("rhodonite-modal");
var jasperBtn = document.getElementById("jasper-button");
var jasperModal = document.getElementById("jasper-modal");
var citrinBtn = document.getElementById("citrin-button");
var citrinModal = document.getElementById("citrin-modal");
var moonCitrinBtn = document.getElementById("moon-citrin-button");
var moonCitrinModal = document.getElementById("moon-citrin-modal");
var azuriteBtn = document.getElementById("azurite-button");
var azuriteModal = document.getElementById("azurite-modal");
var closeModalBtn = document.querySelectorAll(".modal__close");
closeModalBtn.forEach(function (close) {
  close.addEventListener("click", function () {
    modalHide(agateModal);
    modalHide(tourmalineModal);
    modalHide(rhodoniteModal);
    modalHide(jasperModal);
    modalHide(citrinModal);
    modalHide(moonCitrinModal);
    modalHide(azuriteModal);
  });
});
agateBtn.addEventListener("click", function () {
  modalVisible(agateModal);
});
tourmalineBtn.addEventListener("click", function () {
  modalVisible(tourmalineModal);
});
rhodoniteBtn.addEventListener("click", function () {
  modalVisible(rhodoniteModal);
});
jasperBtn.addEventListener("click", function () {
  modalVisible(jasperModal);
});
citrinBtn.addEventListener("click", function () {
  modalVisible(citrinModal);
});
moonCitrinBtn.addEventListener("click", function () {
  modalVisible(moonCitrinModal);
});
azuriteBtn.addEventListener("click", function () {
  modalVisible(azuriteModal);
});

function modalVisible(e) {
  e.style.display = "flex";
}

function modalHide(e) {
  e.style.display = "none";
}