"use strict";

var sortingBtn = document.getElementById("sorting-button");
var sortingBtnIcon = document.getElementById("sorting-button-icon");
var expandableList = document.getElementById("expandable-sorting-list");
var expandableListItems = document.querySelectorAll(".expandable-menu__item");
var products = document.querySelector(".products");
var productsItems = Array.from(document.querySelectorAll(".products__item"));
var sortingBtnText = document.getElementById("sorting-btn-text");
var productsListItems = Array.from(products.children);
var i;

for (i = 0; i < expandableListItems.length; i++) {
  expandableListItems.item(i).setAttribute("data-option", i);
}

for (var _i = 0, _productsListItems = productsListItems; _i < _productsListItems.length; _i++) {
  var _i2 = _productsListItems[_i];

  var getName = _i2.querySelector(".product-card__header");

  var getPrice = _i2.querySelector(".product-card__price");

  var priceValue = parseFloat(getPrice.textContent).toFixed(2);
  var nameValue = getName.textContent;

  _i2.setAttribute("data-price", priceValue);

  _i2.setAttribute("data-name", nameValue);
}

window.addEventListener("click", function (event) {
  if (event.target != sortingBtn) {
    expandableList.classList.remove("list-open");
    sortingBtnIcon.classList.remove("icon-rotate");
    return;
  }

  expandableList.classList.toggle("list-open");
  sortingBtnIcon.classList.toggle("icon-rotate");
});
expandableListItems.forEach(function (item) {
  item.addEventListener("click", function () {
    sortingBtnText.textContent = item.textContent.trim();

    if (item.getAttribute("data-option") == 0) {
      var sorted = productsItems.sort(sortNewest);
      sorted.forEach(function (e) {
        return document.querySelector(".products").appendChild(e);
      });
      return;
    } else if (item.getAttribute("data-option") == 1) {
      var _sorted = productsItems.sort(sortLowToHigh);

      _sorted.forEach(function (e) {
        return document.querySelector(".products").appendChild(e);
      });

      return;
    } else if (item.getAttribute("data-option") == 2) {
      var _sorted2 = productsItems.sort(sortHighToLow);

      _sorted2.forEach(function (e) {
        return document.querySelector(".products").appendChild(e);
      });

      return;
    } else if (item.getAttribute("data-option") == 3) {
      var _sorted3 = productsItems.sort(sortAZ);

      _sorted3.forEach(function (e) {
        return document.querySelector(".products").appendChild(e);
      });

      return;
    } else if (item.getAttribute("data-option") == 4) {
      var _sorted4 = productsItems.sort(sortZA);

      _sorted4.forEach(function (e) {
        return document.querySelector(".products").appendChild(e);
      });

      return;
    }
  });
});

function sortLowToHigh(a, b) {
  return a.dataset.price - b.dataset.price;
}

function sortHighToLow(a, b) {
  return b.dataset.price - a.dataset.price;
}

function sortNewest(a, b) {
  return a.dataset.newest - b.dataset.newest;
}

function sortAZ(a, b) {
  if (a.dataset.name.toString() < b.dataset.name.toString()) return -1;
}

function sortZA(a, b) {
  if (a.dataset.name.toString() > b.dataset.name.toString()) return -1;
}